.tracker-logo {
    width: 160px;
  
}

.nav-button {
  margin: 5px;
  border: 1px solid white;
  background: none;
  color: white;
  cursor: pointer;
}

.nav-button:hover{
  background: white;
  color: #184982;
}

.button-row {
  margin-top: 17px;
  text-align: left;
}

a.button.nav-button {
    text-decoration: none;
    padding: 5px;
}

.header {
  height: 400px;
  background: no-repeat center url("../../images/coloCapitalSizedPixel.jpg");
  background-size: cover;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.header-top {
  background: #fff;
  width: 100%;
  height: 60px;
  margin-top: 20px;
  display: -ms-flexbox;
  display: flex;
 /* padding-top: 30px;*/
}

.header-main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.header-main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
}
.header-text {
  width: 40%;
  color: white;
  text-align: center;
  margin-right: 15px;
}

.nav-list {
  text-align: left;
}

.nav-item {
  padding: 2px;
}


.stat-bar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  height: 70px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 15px 10px;

}

.stat {
  background: #ecebeb;
  width: 33%;
  font-size: 20px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
}

.numbers {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-left: 7px;
}


.big-number {
  font-size: 30px;
  font-weight: 700;
}
.number-description {
    font-size: 14px;
    font-weight: 300;
}

.icon-box {
  -ms-flex-align: center;
      align-items: center;
  background-color: #a2000b;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  font-size: 30px;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  width: 20%;
}


@media screen and (max-width: 760px) {
  .button-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
  }

  .app-header {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack:center;
        justify-content:center;
  }

  .header-top {
    height: 130px;
    margin-top: 0;
  }

  .header {
  height: 400px;
  background: no-repeat center url("../../images/coloCapitalMobile.jpg");
  background-size: cover;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  }

  .header-text{
    width: 90%;
  }

  .stat-bar {
    -ms-flex-direction: column;
        flex-direction: column;
    height: 200px;
    -ms-flex-align: center;
        align-items: center;
  }

  .icon-box {
    height: auto;
  }

  .stat {
    width: 50%;
  }
}

@media screen and (max-width: 550px) {
  .stat {
    width: 80%;
  }
}


